export default {
	data() {
		return {
			user:'',
			point:''
		}
	},
	methods: {
		addPoint()
		{
			if(!this.point)
			{
				uni.showToast({
					title: '积分不能为空!',
					duration: 2000,
					icon:'none'
				});
				return ;
			}
			this.bus.request({
				url: this.bus.url+'/agent/memberCharge',
				method: 'GET',
				data: {
					agentId:this.bus.user.userId,
					// agentId:1,
					userId:this.user.id,
					point:this.point,
					state:1,//1-充值,2-退分
					},
				success: res => {
					res = res.data;
					if(res.status=='1')
					{
						uni.showToast({
							title: '充值成功!',
							duration: 2000
						});
					} else{
						uni.showToast({
							title: '充值失败!',
							duration: 2000,
							icon:'none'
						});
					}
				},
				fail: () => {
					uni.showToast({
						title:'网络错误!',
						duration: 2000,
						icon:'none'
					});
				},
				complete: () => {
					uni.hideLoading()
				}
			});
		}
	},
	onLoad:function (parm) {
		this.user = JSON.parse(parm.data);
	},
}
